type Config = {
  text: string;
  fontSize: number;
  strokeWidth: number;
  fillStyle: string;
};

export class ScratchCard {
  private ctx: CanvasRenderingContext2D;
  private targetCtx: CanvasRenderingContext2D;
  private config: Config = {
    text: "Dummy Text",
    fontSize: 30,
    strokeWidth: 20,
    fillStyle: "pink"
  };

  constructor(
    private readonly canvas: HTMLCanvasElement,
    private readonly targetCanvas: HTMLCanvasElement
  ) {
    const ctx = canvas.getContext("2d");
    const targetCtx = targetCanvas.getContext("2d");

    if (!ctx || !targetCtx) {
      throw new Error("cannot get canvas rendering context");
    }

    this.ctx = ctx;
    this.targetCtx = targetCtx;
  }

  configure(config: Partial<Config>) {
    this.config = {
      ...this.config,
      ...config
    };
  }

  init(width: number, height: number) {
    if (!this.ctx || !this.targetCtx) {
      return;
    }

    this.canvas.width = width;
    this.canvas.height = height;
    this.targetCanvas.width = width;
    this.targetCanvas.height = height;

    this.targetCtx.font = `${this.config.fontSize}px serif`;
    this.targetCtx.fillText(this.config.text, 20, height / 2);
    this.targetCanvas.style.display = "none";

    this.ctx.fillStyle = this.config.fillStyle;
    this.ctx.fillRect(0, 0, width, height);
    this.ctx.lineWidth = this.config.strokeWidth;
    this.ctx.strokeStyle = this.ctx.createPattern(
      this.targetCanvas,
      "no-repeat"
    )!;

    this.canvas.addEventListener("mousedown", (e: MouseEvent) =>
      this.start(e.clientX, e.clientY)
    );

    this.canvas.addEventListener("mouseup", (e: MouseEvent) =>
      this.end(e.clientX, e.clientY)
    );
    this.canvas.addEventListener("touchstart", (e: TouchEvent) =>
      this.start(e.touches[0].clientX, e.touches[0].clientY)
    );
    this.canvas.addEventListener("touchend", (e: TouchEvent) =>
      this.start(e.touches[0].clientX, e.touches[0].clientY)
    );
  }

  private start(x: number, y: number) {
    this.ctx.beginPath();
    this.ctx.moveTo(x, y);
  }

  private end(x: number, y: number) {
    this.ctx.lineTo(x, y);
    this.ctx.stroke();
  }
}
